
/**
 * 返回枚举的键（排除反向映射的数字键）
 */
export function enumKeys<E>(e: E): string[] {
    return Object.keys(e).filter(k => isNaN(Number(k)));
}

/**
 * 返回枚举的 [键, 值] 列表
 */
export function enumEntries<E>(e: E): [string, number | string][] {
    return enumKeys(e).map(k => [k, (e as any)[k]]);
}

/**
 * 返回枚举的值列表（数字或字符串）
 */
export function enumValues<E>(e: E): (number | string)[] {
    return enumEntries(e).map(([_, v]) => v);
}